.banner {
    /*background-image: url('/public/images/busking.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;*/
    color: var(--white);
    height: 100vh;
    position: relative;
    margin-top: -64px;
    padding: 0 8%;
}

.banner--video {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    object-fit: cover;
}

.banner--overlay {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
}

.banner__inner {
    width: 100%;
    margin: 0 auto;
    padding: 32px 0;
    box-sizing: border-box;
    max-width: 1400px;
    height: 100%;
    background-color: transparent;
    z-index: 2;
    position: relative;
    align-content: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.banner__text {
    align-content: center;
    color: var(--white);
    width: fit-content;
    backdrop-filter: blur(2px);
    margin: auto;
    height: fit-content;
    -webkit-mask-image: radial-gradient(circle, rgba(0, 0, 0, 1) 80%, rgba(0, 0, 0, 0) 100%);
    mask-image: radial-gradient(circle, rgba(0, 0, 0, 1) 80%, rgba(0, 0, 0, 0) 100%);
    -webkit-mask-composite: destination-in;
    mask-composite: intersect;
    -webkit-mask-repeat: no-repeat;
    mask-repeat: no-repeat;
  }
  

.soc__container {
    display: flex;
    gap: 16px;
    align-items: center;
    flex-grow: 0;
}

.soc__container svg {
    height: 24px;
    width: 24px;
    cursor: pointer;
    transition: all .5s ease-in-out;
    color:var(--white)
}

.soc__container svg:hover {
    scale: 1.1; 
}

.soc--line {
    flex-grow: 1;
    height: 1px;
    background: rgb(0,0,0);
    background: linear-gradient(90deg, rgba(0,0,0,0) 33.33%, rgba(255,255,255,1) 100%);
}



.banner--title {
    text-transform: uppercase;
    font-size: 6rem;
    font-weight: 700;
}