.button {
    padding: 0.8rem 1rem;
    cursor: pointer;
    transition: all .2s ease-in-out;
    width: fit-content;
}

.button.primary {
    background-color: var(--red);
    color: var(--white);
    border: none;
    outline: 2px solid var(--red);
    outline-offset: -2px;
    -webkit-box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.1);
}

.button.secondary{
    background-color: var(--white);
    color: var(--black);
    border: none;
    outline: 2px solid var(--red);
    outline-offset: -2px;
    -webkit-box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.1);
}

.button.ternary{
    background-color: var(--black);
    color: var(--white);
    border: none;
    outline: 2px solid var(--black);
    outline-offset: -2px;
    -webkit-box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 20px -5px rgba(0, 0, 0, 0.1);
}

.button.ghost {
    background-color: var(--lightGray);
    color: var(--black);
    padding: 0;
    font-size: 0.8rem;
    padding: 0.6rem 0.8rem;
    border: none;
    outline: 1px solid var(--lightGray);
    outline-offset: -1px;
}

.button.link {
    background-color: transparent;
    color: var(--black);
    font-size: 1rem;
    border: none;
}

.button.primary:hover{
    background-color: var(--white);
    color: var(--black);
    outline: 2px solid var(--red);
    outline-offset: -2px;
}

.button.secondary:hover{
    background-color: var(--black);
    color: var(--white);
    border: none;
    outline: 2px solid var(--black);
    outline-offset: -2px;
}

.button.ternary:hover{
    background-color: var(--white);
    color: var(--black);
    border: none;
    outline: 2px solid var(--black);
    outline-offset: -2px;
}

.button.ghost:hover {
    background-color: var(--white);
    border: none;
    outline: 1px solid var(--black);
    outline-offset: -1px;
}

.button.link:hover {
    color: var(--black);
}

.button:disabled {
    opacity: .6;
    cursor: default;
}

.button.small {
    padding: .6rem;
    
}

.icon--button {
    height: 48px;
    width: 48px;
    padding: 10px;
    box-sizing: border-box;
    background-color: transparent;
    transition: all .2s ease-in-out;
    border: none;
    cursor: pointer;
}

.icon--button.primary {
    color: var(--black);
}

.icon--button.secondary{
    color: var(--white);
}

.icon--button.primary:hover {
    color: var(--white);
    background-color: var(--black);
}

.icon--button.secondary:hover{
    background-color: var(--black);
}

.icon--button.ternary {
    background-color: var(--black);
    color: var(--white);
}

.icon--button.ternary:hover {
    background-color: var(--white);
    color: var(--black);
}

.icon--button * {
    width: 100%;
    height: 100%;
    color: inherit;
}


.button:disabled {
    cursor: no-drop;
}